@import '../../styles/variables';
@import '../../styles/breakpoints';



.formContainer {
  position: relative;
}

.form {
  display: flex;
  flex-direction: row;

  :global(.ant-form-explain) {
    margin-top: 8px;
    padding: 0;
    min-height: 0;
    font-weight: 300;
    font-size: 12px;
    line-height: 12px;
    color: $red2;
  }

  :global(.ant-form-explain ~ .ant-form-extra) {
    display: none;
  }

  :global(.ant-form-extra) {
    min-height: 0;
    margin-top: 6px;
    padding: 0;
    font-weight: 300;
    font-size: 12px;
    line-height: 16px;
    color: $light8;
  }

  :global(.ant-col) {
    position: static;
  }

  :global(.ant-form-item-control) {
    position: static;
    line-height: normal;
    margin-bottom: 25px;

    @include sm {
      margin-bottom: 0;
    }
  }

  :global(.ant-form-item-children) {
    position: static;
  }

  :global(.ant-row) {
    position: static;
  }

  :global(.ant-form-item) {
    position: static;
    margin-bottom: 0;
  }

  :global(.ant-form-item-control.has-error) {
    :global(.ant-form-item-children) {
      > div {
        > div {
          > div {
            border-color: $red2 !important;
          }
        }
      }

      input {
        border-color: $red2 !important;
      }
    }
  }
}

.row {
  display: flex;
  flex-direction: column;

  @include sm {
    flex-direction: row;
    margin-bottom: 25px;
  }

  @include md {
    margin-bottom: 40px;
  }

  :global(.ant-form-item) {
    @include sm {
      flex: 1 1;
    }
  }

  :global(.ant-form-item):first-child {
    @include sm {
      margin-right: 51px;
    }

    @include md {
      margin-right: 50px;
    }
  }
}

.singleInRow {
  margin-right: 0 !important;
}

.fieldsWithInfo {
  margin-bottom: 0;
}

.services {
  height: unset;
  margin: 0 -28px 20px 0; // margins with design spaces and checkbox margins

  @include sm {
    margin: 0 -26px 15px 0; // margins with design spaces and checkbox margins
  }

  @include md {
    margin: 0 -15px 30px 0;
  }

  :global(.ant-form-item-control) {
    margin-bottom: 0;
  }
}

.formTextArea {
  height: unset;

  :global(.ant-form-item-control) {
    margin-bottom: 0;
  }
}

.formControls {
  margin-bottom: 0;
  :global(.ant-form-item-control) {
    margin-bottom: 0;
  }
}

.contorlsRow {
  height: unset;
}

